$content-width: 800px;
$main: #050369;
$font-style: 'Open Sans', sans-serif;
$font-color: #424242;
$link-color: #02336a;
$link-hover-color: $main;
$heading-font: 'Muli', sans-serif;
$heading-font-color: #000000;
$light: #E7EDF4;
$header:  #dbd8f0;

*,
*::before,
*::after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
} 